import Vue from 'vue'
import VueRouter from 'vue-router'
import SongMenu from './components/SongMenu'


Vue.config.productionTip = false
Vue.use(VueRouter)
const routes = [
    {
        path: '/SongMenu',
        component: resolve => {
            return require(["./components/SongMenu"], resolve);
        },
        // component: SongMenu,

    },
    // {path: '/', redirect: '/SongMenu'},
];
const router = new VueRouter({
    routes
})
export default router;